.navbarDiv{
    top: 0;
    width: 100%;
    height: 60px;
    background: #ffffff1a;
    box-shadow: 0 4px 30px #0000001a;
    -webkit-backdrop-filter: blur(5px);
    backdrop-filter: blur(5px);
    display: flex;
    position: sticky;
    z-index: 10;
    align-items: center;
    justify-content: space-between;
}

.menuNavBar{
  padding: 0px 10px;
  color: white;
}

.buttonMenu{
  width: 100px;
  padding: 0 10px;
  background: transparent;
  border: none;
  cursor: pointer;
}

/* Logo centrado */
  
.redesStyleContainer{
  display: none;
}

.redesStyle{
    display: none;
}

@media only screen and (max-width: 550px) {
  .logoLink{
    padding: 0 30px 0 0;
  }
}

.logoLink{
  display: block;
  margin-left: auto;
  height: 40px;
  flex: 0 0 auto;
}

/* Responsive logo styles */
.logoMobile {
  display: block !important;
  height: 100%;
  width: auto;
  max-width: 100px;
  object-fit: contain;
}

.logoDesktop {
  display: none !important;
  height: 100%;
  width: auto;
  max-width: 100px;
  object-fit: contain;
}

/* Desktop breakpoint - show desktop logo, hide mobile logo */
@media (min-width: 768px) {
  .logoMobile {
    display: none !important;
  }
  
  .logoDesktop {
    display: block !important;
    max-width: 220px;
  }
}

.textosRedesStyle{
  color: white;
  font-size: 12px;
  text-decoration: none;
  display: none;
}

.redesIcons{
  display: none;
}

@media only screen and (min-width: 550px) {

    .redesStyleContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    gap: 10px;
    margin-right: 20px;
    width: auto;
  }


  .redesStyle {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    gap: 5px;
  }

  .redesStyle > div {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  .redesIcons{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-right: 5px;
  }
}


@media only screen and (min-width: 1400px) {

  .textosRedesStyle{
    display: flex;
  }

  .menuNavBar{
    width: 430px;
  }

  .redesIcons:nth-child(0){
    display: flex;
    position: absolute;
    right: 20%;
  }

}